// pages/movies.tsx
import { GetStaticProps } from "next";
import MovieCard from "@/components/MovieCard";

// Define the structure of a Movie
interface Movie {
  imgURL: string;
  imgAlt: string;
  buttonText: string;
}

interface MoviesAPIProps {
  movies: Movie[];
}

export default function MoviesAPI({ movies }: MoviesAPIProps) {
  return (
    <div className="flex justify-center items-center mx-auto">
      <div className="grid gap-8 md:grid-cols-2 items-center text-center">
        {movies.map((movie, index) => (
          <div key={index} className="flex justify-center">
            <MovieCard
              imgAlt={movie.imgAlt}
              imgURL={movie.imgURL}
              buttonText={movie.buttonText}
            />
          </div>
        ))}
      </div>
    </div>
  );
}

// Fetch data at build time
export const getStaticProps: GetStaticProps = async () => {
  try {
    const response = await fetch(
      "http://192.168.0.192:7072/api/GetMovie/?id=1"
    );
    const data = await response.json();

    // Map the API response to the Movie interface
    const movies: Movie[] = [
      {
        imgURL: data.coverURL,
        imgAlt: data.Title,
        buttonText: "Notify me",
      },
    ];

    return {
      props: {
        movies,
      },
      revalidate: 60, // Revalidate every 60 seconds (optional)
    };
  } catch (error) {
    console.error("Error fetching movies:", error);
    return {
      props: {
        movies: [],
      },
    };
  }
};
